.work-item{position:relative;display:block;float:left;width:30%;margin:20px 1.5%;padding-bottom:47%;color:#555;overflow:hidden}.work-item .front,.work-item .back{position:absolute;left:0;top:0;width:100%;height:100%;background-size:100% auto;background-position:0 100%}.work-item .front{background-color:#fff;text-align:center;padding:10px;box-sizing:border-box;-webkit-transition:all .377s;transition:all .377s;-webkit-transform:translateY(86%);transform:translateY(86%);box-shadow:-1px 0 4px rgba(0,0,0,0.2)}.work-item .front p{font-size:.9em}.work-item:hover .front{-webkit-transform:translateY(0);transform:translateY(0)}.work-item .qr-code{text-align:center;margin:20px 0}.work-item .qr-code img{display:inline-block !important;max-width:100%}.work-item .title{margin:0;padding:10px;font-size:18px;font-weight:normal;overflow:hidden;text-overflow:ellipsis;white-space:nowrap}


